export class Colors {
  private static _isDarkTheme: boolean = false;
  private static _isBigFonts: boolean = false;

  public static success = Colors.getCssColor('success');
  public static danger = Colors.getCssColor('danger');
  public static warning = Colors.getCssColor('warning');
  public static black = Colors.getCssColor('black');
  public static light = Colors.getCssColor('light');
  public static stable = Colors.getCssColor('stable');

  public static hexToRgb(hex: string, opacity = 1) {
    let bigint = parseInt(hex.replace('#', ''), 16);
    let r = (bigint >> 16) & 255;
    let g = (bigint >> 8) & 255;
    let b = bigint & 255;
    return 'rgba(' + r + ',' + g + ',' + b + ',' + opacity + ')';
  }

  public static getKeys() {
    return ['success', 'danger', 'black', 'warning', 'stable'];
  }

  public static getCssColor(color: string, defaultValue?: string) {
    if (document) {
      let retVal = getComputedStyle(document.body).getPropertyValue('--' + color) || defaultValue || Colors[color.replace('-color', '')];
      retVal = (retVal || '').trim();
      return retVal;
    }
    return defaultValue || Colors[color];
  }

  public static lightenDarkenColor(color: string, amount: number) {
    let usePound = false;
    if (color[0] === '#') {
      color = color.slice(1);
      usePound = true;
    }

    let num = parseInt(color, 16);

    let r = (num >> 16) + amount;
    if (r > 255) {
      r = 255;
    } else if (r < 0) {
      r = 0;
    }

    let b = ((num >> 8) & 0x00ff) + amount;
    if (b > 255) {
      b = 255;
    } else if (b < 0) {
      b = 0;
    }

    let g = (num & 0x0000ff) + amount;
    if (g > 255) {
      g = 255;
    } else if (g < 0) {
      g = 0;
    }

    return (usePound ? '#' : '') + (g | (b << 8) | (r << 16)).toString(16);
  }

  public static toggleDarkTheme() {
    Colors.setDarkTheme(!Colors.isDarkTheme());
  }

  public static isDarkTheme() {
    return Colors._isDarkTheme;
  }

  public static setDarkTheme(enabled) {
    Colors._isDarkTheme = enabled;
    if (document) {
      if (enabled) {
        document.documentElement.classList.add('dark-theme');
      } else {
        document.documentElement.classList.remove('dark-theme');
      }

      Colors.success = Colors.getCssColor('success');
      Colors.danger = Colors.getCssColor('danger');
      Colors.warning = Colors.getCssColor('warning');
      Colors.black = Colors.getCssColor('black');
      Colors.light = Colors.getCssColor('light');
      Colors.stable = Colors.getCssColor('stable');
      //Colors.text = Colors.getCssColor('always-text-color');
    }
  }

  public static toggleBigFonts() {
    Colors.setBigFonts(!Colors.isBigFonts());
  }

  public static isBigFonts() {
    return Colors._isBigFonts;
  }

  public static setBigFonts(enabled) {
    Colors._isBigFonts = enabled;
    if (document) {
      if (enabled) {
        document.documentElement.classList.add('big-fonts');
      } else {
        document.documentElement.classList.remove('big-fonts');
      }
    }
  }
}
