import { Injectable, Inject, PLATFORM_ID, InjectionToken } from '@angular/core';
import { isPlatformServer } from '@angular/common';
import {
  ICoreConfig,
  ISyncedCollection,
  setIsWeb,
  setIsIonic,
  isIOS as _isIOS,
  isCordova as _isCordova,
  isCapacitorNative as _isCapacitorNative,
  isElectron as _isElectron,
  isFirefox as _isFirefox,
  isIE as _isIE,
  isIE11 as _isIE11,
  isIOS9 as _isIOS9,
  isIphoneX as _isIphoneX,
  isIphone5 as _isIphone5,
  isIphoneSE as _isIphoneSE,
  isAndroid as _isAndroid,
  isWKWebView as _isWKWebView,
  isSamsung as _isSamsung,
  isZebraScanner as _isZebraScanner,
  isTablet as _isTablet,
  isPhablet as _isPhablet,
  isSurface as _isSurface,
  getProtocol as _getProtocol
} from '@shared/stencil';

export const CORECONFIG_CONSTANTS = new InjectionToken<any>('CoreConfigConstants');

@Injectable()
export class CoreConfig implements ICoreConfig {
  private configConstants: any = {};

  constructor(@Inject(PLATFORM_ID) private platformId, @Inject(CORECONFIG_CONSTANTS) config) {
    this.configConstants = config;
  }

  public setStencil() {
    setIsWeb(this.isWeb());
    setIsIonic(this.isIonic());
  }

  public isWeb(): boolean {
    return this.configConstants.configIsWeb;
  }

  public isIonic(): boolean {
    return this.configConstants.configIsIonic2;
  }

  public isCordova(): boolean {
    return _isCordova();
  }

  public isCapacitorNative(): boolean {
    return _isCapacitorNative();
  }

  public isHybrid() {
    return this.isCordova() || this.isCapacitorNative();
  }

  public isElectron(): boolean {
    return _isElectron();
  }

  public isFirefox(): boolean {
    return _isFirefox();
  }

  public isIE(): boolean {
    return _isIE();
  }

  public isIE11(): boolean {
    return _isIE11();
  }

  public isUniversal(): boolean {
    return isPlatformServer(this.platformId);
  }

  public getPlatform(): string {
    return this.configConstants.configPlatform;
  }

  public getPlatformClean(): string {
    return this.getPlatform()
      .toLowerCase()
      .replace('ionic', 'mobile');
  }

  public isIOS() {
    return _isIOS();
  }

  public isIOS9() {
    return _isIOS9();
  }

  public isIphoneX() {
    return _isIphoneX();
  }

  public isIphone5() {
    return _isIphone5();
  }

  public isIphoneSE() {
    return _isIphoneSE();
  }

  public isAndroid() {
    return _isAndroid();
  }

  public isWKWebView() {
    return _isWKWebView();
  }

  public isTablet() {
    return _isTablet();
  }

  public isPhablet() {
    return _isPhablet();
  }

  public isSamsung() {
    return _isSamsung();
  }

  public isZebraScanner() {
    return _isZebraScanner();
  }

  public isSurface() {
    return _isSurface();
  }

  public isLocalhost() {
    return location.hostname === 'localhost' || location.hostname === '127.0.0.1';
  }

  public reload(cleanLocation: boolean = false) {
    if (this.isElectron()) {
    } else if (location && (<any>location).reload) {
      location.reload();
      if (cleanLocation) {
        location.replace(location.origin);
      }
    }
  }

  public isFullScreenEnabled() {
    if (this.isUniversal()) {
      return false;
    }
    return (<any>document).fullscreenEnabled || (<any>document).webkitFullscreenEnabled || (<any>document).mozFullScreenEnabled || (<any>document).msFullscreenEnabled;
  }

  public isFullScreen() {
    if (this.isUniversal()) {
      return false;
    }
    if ((<any>document).fullscreenElement || (<any>document).webkitFullscreenElement || (<any>document).mozFullScreenElement || (<any>document).msFullscreenElement) {
      return true;
    }
    return false;
  }

  public requestFullScreen() {
    if (this.isUniversal()) {
      return;
    }
    if (document && document.body) {
      let i: any = document.body;
      // go full-screen
      if (i.requestFullscreen) {
        i.requestFullscreen();
      } else if (i.webkitRequestFullscreen) {
        i.webkitRequestFullscreen();
      } else if (i.mozRequestFullScreen) {
        i.mozRequestFullScreen();
      } else if (i.msRequestFullscreen) {
        i.msRequestFullscreen();
      }
    }
  }

  public exitFullScreen() {
    if (this.isUniversal()) {
      return;
    }
    if (document) {
      if ((<any>document).exitFullscreen) {
        (<any>document).exitFullscreen();
      } else if ((<any>document).webkitExitFullscreen) {
        (<any>document).webkitExitFullscreen();
      } else if ((<any>document).mozCancelFullScreen) {
        (<any>document).mozCancelFullScreen();
      } else if ((<any>document).msExitFullscreen) {
        (<any>document).msExitFullscreen();
      }
    }
  }

  public getProtocol(): string {
    return _getProtocol();
  }

  public getMode(): string {
    return this.configConstants.configMode;
  }

  public getKey(key: string): string {
    return this.configConstants[key];
  }

  public getAppId(): string {
    return window['BuildInfo'] ? window['BuildInfo'].basePackageName : window.location.hostname;
  }

  public getAppVersion(): string {
    return this.configConstants.appVersion;
  }

  public getAppName(): string {
    return this.configConstants.appName;
  }

  public getFullAppName(): string {
    return this.getAppName() + '-' + this.getPlatformClean() + '-' + this.getAppVersion();
  }

  public getWebUrl() {
    return this.configConstants.webUrl;
  }

  public getCssPublicUrl() {
    //https://storage.googleapis.com/yoobic-mobile-apps/6.0.222-1/operations/mobile/styles.f3f4a35e34aff00b5977.css
    return 'https://storage.googleapis.com/yoobic-mobile-apps/' + this.getAppVersion() + '/' + this.getAppName() + '/' + this.getPlatformClean() + '/';
  }

  public getSyncedCollections(): Array<ISyncedCollection> {
    return this.configConstants.syncedCollections;
  }
}
